.vertical {
    /* Apparence */
    border: solid gray 1px;
    /* Dimensions */
    width: 19rem;
    height: 30rem;
}

.horizontal {
    /* Apparence */
    border: solid gray 1px;
    background-position: center;
    /* Dimensions */
    width: 32rem;
    height: 22rem;
    /* Positioning */
    margin-top: 7rem;
}

article a {
    /* Positioning */
    position: absolute;
    bottom: 10%;
}

@media all and (max-width: 992px) {
    .vertical, 
    .horizontal {
        margin-top: 2rem;
    }
    .horizontal {
        width: 90%;
    }
}